import helper from "../../../common/helper.js"
export default {
	data() {
		return {
			dangerRoad: '',
			showDangerCheckBox: false,
			dangerInfoRow: [],
			dangerCheckBox: [],
			current: -1,
			choose: '',
			ROLEID: 0,
			today: '',
			originalData: [],
			place: '',
			source: [],
			newdec: '',
			chooseed: {},
			show: false,
			modalName: null
		}
	},
	mounted() {
		this.ROLEID = this.$store.state.userInfo.ROLEID;
		this.today = helper.now();
		this.getInfo();
		console.log(this.ROLEID)
	},
	methods: {
		showMoadel(title) {
			uni.showModal({
				title: '提示',
				content: title,
				showCancel: false,
				success: function(res) {
					if (res.confirm) {}
				}
			});
		},
		reset() {
			this.dangerInfoRow = this.originalData;
			this.dangerCheckBox = [];
			this.showDangerCheckBox = false;
			this.dangerRoad = '';
			this.choose = '';
			this.current = -1;
		},
		getInfo() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Get_AllBZ',
				data: {
					DWCD: self.$store.state.userInfo.DWCD,
					// RYXH: self.$store.state.userInfo.RYXH
				},
				method: 'GET',
				header: {
					'content-type': 'application/json' // 默认值
				},
				success(res) {
		
					let data = JSON.parse(res.data)
					console.log(data)
					if (res.data.length == 0) {
						self.showMoadel('此处没有隐患源')
					} else {
						self.originalData = data;
						self.dangerInfoRow = data;
					}
				}
			})
		},
		radioChange: function(evt) {
			// this.getPCYD(evt.target.value)
			for (let i = 0; i < this.dangerInfoRow.length; i++) {
				if (this.dangerInfoRow[i].XZCD === evt.target.value) {
					this.current = i;
					this.choose = evt.target.value;
					this.dangerRoad = this.dangerInfoRow[i].XZMC
					break;
				}
			}
		},
		getNext(item) {
			console.log(item);
			var self = this;
			uni.showLoading({
				title: '加载中',
				mask:true
			});
			
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Choose_YS2',
				method: 'GET',
				data: {
					id: item.XZCD,
					RYXH: self.$store.state.userInfo.RYXH
				},
				header: {
					'custom-header': 'application/json'
				},
				success: res => {
					let data = JSON.parse(res.data);
					console.log(data);
					self.dangerCheckBox = data;
					self.dangerRoad = self.dangerRoad + item.XZMC;
					self.showDangerInfo = false;
					self.showDangerCheckBox = true;
					uni.hideLoading();
				}
			});
		},
		search() {
			var self = this;
			if (!self.choose) {
				self.showMoadel('请选择工点!');
			} else {
				uni.showLoading({
					title:'查询中。。。',
					mask:true
				});
				setTimeout(() => {
					uni.request({
						url: `${self.$store.state.urlPrefix}/home/Get_JournalAccount`,
						data: {
							XZCD: self.choose
						}
					}).then(data => {
						var [error, res] = data;
						// console.log(res.data)
						if (res.data != '[]') {
							console.log(JSON.parse(res.data))
							self.source = JSON.parse(res.data);
							self.showDangerCheckBox = true;
						} else {
							self.showMoadel('此处没有危险源！')
						}
						uni.hideLoading();
					})
				},1000)
			}
			
		},
		commit() {
			var self = this;
			if (!self.choose) {
				self.showMoadel('请选择工点！');
			} else if (!self.place) {
				self.showMoadel('请输入危险源！');
			} else {
				uni.showLoading({
					title:'提交中。。。',
					mask: true
				})
				setTimeout(() => {
					uni
						.request({
							url: `${self.$store.state.urlPrefix}/home/Add_JournalAccount`,
							data: {
								content: self.place,
								userid: self.$store.state.userInfo.RYXH,
								XZCD: self.choose
							}
						})
						.then(data => {
							var [error, res] = data;
							
							uni.hideLoading();
							
							if (res.data.code == 0) {
								self.showMoadel('添加成功')
							} else {
								self.showMoadel('添加失败')
							}
							self.place = '';
							self.reset();
						});
				},1000)
			}
		},
		deleteItem(item) {
			var self = this;
			uni.showModal({
			    title: '提示',
			    content: '是否删除该危险源？',
					confirmColor: '#F56C6C',
			    success: function (res) {
			        if (res.confirm) {
			            uni.showLoading({
			            	title: '删除中。。。',
										mask:true
			            })
									setTimeout(() => {
										uni.request({
											url: `${self.$store.state.urlPrefix}/home/Delete_JournalAccount`,
											data: {
												id: item.ID,
												userid: self.$store.state.userInfo.RYXH,
											}
										}).then(data => {
											var [error, res] = data;
											if (res.data.code == 0) {
												self.showMoadel('删除成功');
												for (let i=0;i<self.source.length;i++) {
													if (self.source[i].ID == item.ID) {
														self.source.splice(i,1)
													}
												}
											} else {
												self.showMoadel('删除失败')
											}
											uni.hideLoading();
										})
									},1000)
			        } else if (res.cancel) {
			            
			        }
			    }
			});
		},

		showModal(e) {
			this.newdec = e.currentTarget.dataset.target.Content;
			this.chooseed = e.currentTarget.dataset.target;
			this.modalName = "Modal"
			console.log(e)
		},
		hideModal(e) {
			this.modalName = null
		},
		editName() {
			var self = this;
			self.modalName = null;
			uni.showLoading({
				title:'修改中。。。',
				mask:true
			});
			setTimeout(() => {
				uni.request({
					url: `${self.$store.state.urlPrefix}/home/Amend_JournalAccount`,
					data: {
						content: self.newdec,
						id: self.chooseed.ID
					}
				}).then(data => {
					var [error, res] = data;
					console.log(res.data)
					if (res.data.code == 0) {
						self.showMoadel('修改成功')
						for (let i=0;i<self.source.length;i++) {
							if (self.source[i].ID == self.chooseed.ID) {
								self.source[i].Content = self.newdec;
							}
						}
					} else {
						self.showMoadel('修改失败')
					}
					uni.hideLoading();
				})
			},1000)
		}
	}
}
